@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap");

:root {
  --background-color: #ebebeb;
  --foreground-color: #0a192f;
  /* --background-color: #0a192f;
  --foreground-color: #64faff; */
  /* --transition-speed: 600ms; */
  --navbar-width: 4rem;
  /* --navbar-hover-expand: 1.2; */
  --navbar-height: 90vh;
  --navbar-padding: 1rem;
  --brand-size: 2rem;
  --icon-size: 1.4rem;
  --icon-stroke: 1.5px;
  --item-height: 4rem;
}

html {
  font-size: 14px;
}

body {
  background-color: var(--background-color);
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: var(--background-color);
}

body::-webkit-scrollbar-thumb {
  background: var(--foreground-color);
}

.navbar {
  position: fixed;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.nav-item {
  width: 100%;
  border-radius: 8px;
}

.nav-link > svg {
  width: 3rem;
  stroke: var(--background-color);
  /* transition: stroke 0.25s ease-in-out; */
}

.brand-link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--item-height);
}

.brand-link > svg > g > text {
  fill: var(--background-color);
}

.brand {
  width: var(--brand-size);
}

.content > h1 {
  font-family: "Fira Code";
  font-weight: 600;
}

.content > p {
  font-family: "Fira Code";
  font-weight: 200;
}

@media only screen and (max-width: 1000px) {
  .content {
    margin: 0 auto;
    width: calc(100vw - 4.25rem);
    padding: 2rem;
    color: var(--foreground-color);
    text-align: center;
  }

  .navbar {
    bottom: 0;
    width: calc(100vw - 1rem);
    height: 5rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-item:first-child {
    display: none;
  }

  .nav-item {
    background-color: var(--foreground-color);
    justify-content: center;
    align-items: center;
    display: flex;
    border-radius: 0rem;
  }

  .nav-item:hover {
    background-color: rgba(10, 25, 47, 0.5);
    justify-content: center;
    align-items: center;
    display: flex;
    border-radius: 0rem;
  }
  .nav-item:nth-child(2) {
    border-top-left-radius: 3rem;
    border-top-right-radius: 0rem;
    border-bottom-left-radius: 3rem;
    border-bottom-right-radius: 0rem;
  }

  .nav-item:last-child {
    border-top-left-radius: 0rem;
    border-top-right-radius: 3rem;
    border-bottom-left-radius: 0rem;
    border-bottom-right-radius: 3rem;
  }

  .nav-link > svg {
    height: var(--icon-size);
    width: var(--icon-size);
    stroke-width: var(--icon-stroke);
    justify-content: space-evenly;
  }
}

@media only screen and (min-width: 1000px) {
  .content {
    margin: 0 auto;
    width: calc(100vw / 1.4);
    padding: 2rem;
    color: var(--foreground-color);
    text-align: center;
  }

  .navbar {
    width: var(--navbar-width);
    height: var(--navbar-height);
    top: calc(calc(100vh - var(--navbar-height)) / 2);
    background-color: rgba(10, 25, 47, 0.8);
    border-radius: 2rem;
    margin-left: 1.5rem;
  }

  .navbar-nav {
    flex-direction: column;
  }

  .nav-item:first-child {
    padding-top: var(--navbar-padding);
  }

  .nav-item:nth-child(2) {
    margin-top: auto;
  }

  .nav-item > .nav-link:hover > svg {
    stroke: var(--foreground-color);
  }

  .nav-item:last-child {
    margin-top: auto;
    padding-bottom: var(--navbar-padding);
  }

  .nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: var(--item-height);
  }

  .nav-link > svg {
    height: var(--icon-size);
    width: var(--icon-size);
    stroke-width: var(--icon-stroke);
  }
}
